import Vue from 'vue'
import Router from 'vue-router'
import index from '@/page/index'
import list from '@/page/list'
import login from '@/main/webapp/html/account/login'
Vue.use(Router)

export default[
  {
    path: '/',
    component: index
  },
  {
    path: '/list',
    component: list
  },{
    path:'/login',
    component:login
  }
]




